/** @format */

.hero {
	margin: 0 3vw;

	padding: 3vw;
	padding-left: 0;
}
.navbar {
	align-items: center;
	display: flex;
	justify-content: space-between;
	margin: 1.5vw 3vw;
}
.header2 {
	color: #303030;
	font-family: Century Gothic;
	font-size: 48px;
	font-size: 4.8rem;
}

.contactbar {
	align-items: center;
	background-color: rgba(0, 175, 65, 1);
	box-sizing: border-box;
	display: flex;
	height: 70px;
	justify-content: flex-end;
	padding: 3vw;
	width: 100vw;
}
.print {
	align-items: center;
	color: black;
	display: flex;
	font-size: 2rem;
}
.print > span {
	font-size: 3rem;
	font-weight: 900;
	padding-bottom: 5px;
}

.print > button {
	font-size: 1rem;
	display: flex;
	align-items: center;
	margin: 1rem;
}

.breadcrumbs {
	margin-right: 1rem;
	text-decoration: underline;
}

.breadcrumbsPrintBox {
	align-items: center;
	display: flex;
	gap: 3vw;
	justify-content: flex-end;
}
@media screen and (max-width: 600px) {
	.breadcrumbsPrintBox {
		align-items: center;
		display: flex;
		justify-content: space-between;
		width: 90vw;
	}
}
