/** @format */

.searchName {
	align-items: center;
	background: grey;
	display: flex;
	grid-gap: 3vw;
	gap: 3vw;
	padding: 0 3vw;
	width: 100%;
}
.searchName input {
	height: 3vw;
	width: 90%;
	padding: 2rem;
}
.searchName div {
	width: 90%;
}
label[for="searchbyname"] {
	color: rgba(0, 150, 75, 2);
	color: oldlace;
	color: rgba(250, 250, 250, 1);
	text-shadow: 1px -0.1px 0px green, -1px 2px 0px black, 2px -2px 3px black,
		-2px 2px 3px black;
	font-weight: 600;
	/* height: 300px; */
	display: flex;
	vertical-align: middle;
	align-items: center;
	font-size: 3rem;
	gap: 3vw;
}
.searchLocation--inputs {
	background: lightgreen;
	display: flex;
	gap: 3vw;
	padding: 0 3vw;
}

.searchLocation--display {
	padding: 1vw;
}
