/** @format */

.skeleton {
	background: #ddd;
	margin: 1rem 0;
	border-radius: 0.4rem;
}

.skeleton.text {
	width: 100%;
	height: 1.2rem;
}
.skeleton.title {
	width: 50%;
	height: 2rem;
	margin-bottom: 1.5rem;
}
.skeleton.avatar {
	width: 10rem;
	height: 10rem;
	border-radius: 50%;
}
.skeleton.thumbnail {
	width: 10rem;
	height: 10rem;
}

.skeleton-wrapper {
	margin: 2rem auto;
	padding: 1rem 1.5rem;
	border-radius: 0.4rem;
	position: relative;

	/* hides the over of shimmer animation */
	overflow: clip;
}
.skeleton-profile {
	display: grid;
	grid-template-columns: 1fr 2fr;
	gap: 3rem;
	align-items: center;
}

.skeleton-wrapper.light {
	background: #f2f2f2;
}
.skeleton-wrapper.dark {
	background: #444;
}
.skeleton-wrapper.dark .skeleton {
	background: #777;
}
/* animation effects */

.shimmer-wrapper {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	animation: loading 2.5s infinite;
	transform: skewX(-20deg);
}

.shimmer {
	width: 50%;
	height: 100%;
	/* padding-bottom:1rem; */

	background: rgba(255, 255, 255, 0.2);
	transform: skewX(-20deg);
	box-shadow: 0 0 30px 30px rgba(255, 255, 255, 0.05);
	filter: brightness(0.82);
}
.dark .shimmer {
	background: linear-gradient(
		-20deg rgba(2, 254, 254, 0.9),
		rgba(222, 222, 222, 0.4)
	);
	transform: skewX(-20deg);
	filter: brightness(0.95);
}

@keyframes loading {
	0% {
		transform: translateX(-30%);
	}
	50% {
		transform: translateX(60%);
	}
	100% {
		transform: translateX(160%);
	}
}
