/** @format */
* {
	box-sizing: border-box;
}
.mainContainer {
	display: flex;
	font-size: 1.4rem;
	justify-content: space-between;
	flex-wrap: wrap;
	gap: 3vw;
	width: 100%;
}

/* left column*/
.left {
	display: flex;
	flex-flow: column wrap;
	gap: 3vw;
	align-content: space-between;
	width: 61vw;
}

.panel {
	border: solid 2px black;
	display: flex;
	padding: 1rem 0;
	height: auto;
}
.panel .subHeader {
	flex-flow: column;
	gap: 3vw;
	justify-content: space-evenly;
	border: none;
}

.panelRow {
	align-items: center;
	flex-flow: row;
	gap: 1vw;
	justify-content: flex-start;
}
.rightBlock {
	display: flex;
	flex-flow: column;
	justify-content: space-around;
	gap: 1vw;
}

.rightBlock .btns {
	display: flex;
	flex-flow: row wrap;
	gap: 1vw;
}
.rightBlock .btn-success {
	width: 30%;
}
/* right column*/

.right {
	display: flex;
	flex-flow: column;
	min-width: 25vw;
	width: 20vw;
	gap: 3vw;
}

.sidebar--container {
	display: flex;
	flex-flow: column;
	gap: 3vw;
	height: 100%;
	justify-content: space-between;
	width: 100%;
}

.sidebarItem {
	display: flex;
	flex-flow: column;
	padding: 1rem;
	justify-content: space-evenly;
	border: solid 1px rgba(10, 210, 10, 0.2);
}
.sidebarItem--header {
	background: rgb(0, 175, 65);
	color: white;
	width: 100%;
	font-size: 2rem;
	padding: 1rem;
}

h3 {
	font-size: 2.4rem;
}
h2 {
	font-size: 3.6rem;
}
h1 {
	font-size: 4.8rem;
}
.btn-success {
	align-items: center;
	background-color: #00af41;
	border-color: #198754;
	border-radius: 5px;
	color: #fff;
	display: flex;
	font-size: 1.4rem;
	font-weight: 500;
	padding: 1rem 0.25rem;
	place-content: center;
	text-align: center;
	text-decoration: none;
	width: 90%;
}
.popupContainer {
	display: flex;
	flex-flow: column;
	gap: 3vw;
}
.sidebarItem a {
	text-decoration: none;
	color: rgb(0, 175, 65);
}
.popupBtn {
	display: flex;
	flex-direction: row;
	gap: 3vw;
}

/* .left {
	width: 100%;
	display: flex;
	flex-flow: row wrap;
} */

.right {
	min-width: 100%;
	width: auto;
	margin: 0 auto;
	border: solid 1rem rbga(10, 210, 10, 0.4);
}
.subHeader {
	flex-flow: column;
	gap: 3vw;
	justify-content: space-evenly;
	border: none;
	width: 100%;
}
.subHeader .btn-success {
	width: 100%;
}
.rightBlock .btn-success {
	width: 90%;
}
/* .hero > img {
	width:100%;
	padding: 3vw;
} */

/* mobile */
.sidebarItem {
	display: flex;
	flex-flow: column wrap;
	padding: 1rem;
	justify-content: space-between;
	border: solid 1px rgba(10, 210, 10, 0.2);
	min-width: 45vw;
	width: 100%;
}

.rightBlock .btn-success {
	width: 90%;
}

@media screen and(min-width:600px) {
	/* tablet */

	.mainContainer {
		display: flex;
		font-size: 1.4rem;
		justify-content: space-between;
		flex-flow: column;
		gap: 3vw;
		width: 100%;
	}
	.left {
		display: flex;
		flex-flow: row wrap;
		grid-gap: 3vw;
		gap: 3vw;
		align-content: space-between;
		/* min-width: 100%;
		width: 100%; */
	}
	.subHeader {
		flex-flow: column;
		grid-gap: 3vw;
		gap: 3vw;
		justify-content: space-evenly;
		border: none;
		/* width:100%; */
		width: 100%;
	}
	.subHeader .btn-success {
		width: 100%;
	}

	.left .rightBlock p {
		padding: 0 1rem;
	}
	.right {
		display: flex;
		flex-flow: column;
		gap: 3vw;
		grid-gap: 3vw;
		min-width: 25vw;
		width: 100%;
	}
	.sidebar--container {
		display: flex;
		flex-flow: row wrap;
		gap: 3vw;
		grid-gap: 3vw;
		height: 100%;
		justify-content: space-between;
		width: 100%;
	}
	.sidebarItem {
		border: solid 1px rgba(10, 210, 10, 0.2);
		display: flex;
		flex-flow: column;
		justify-content: space-between;
		min-width: 45vw;
		padding: 1rem;
		width: 45%;
	}
}
@media screen and(min-width:900px) {
	/* desktop */
	.mainContainer {
		display: flex;
		flex-wrap: nowrap;
		font-size: 1.4rem;
		gap: 3vw;
		justify-content: space-between;
		width: 100%;
	}

	.main-container .left {
		/* align-content: space-between; */
		display: flex;
		flex-flow: column nowrap;
		gap: 3vw;
		/* width: 60vw; */
	}
	.left .panel .subHeader {
		border: none;
	}
	.rightBlock .btns .btn-success {
		width: 30%;
	}

	.main-container .right {
		display: flex;
		flex-flow: column;
		gap: 3vw;
		margin: 0 auto;
		min-width: 24vw;
		width: 24vw;
	}

	.sidebar--container {
		display: flex;
		flex-flow: column;
		grid-gap: 3vw;
		gap: 3vw;
		height: 100%;
		justify-content: space-between;
		width: 100%;
	}
	.sidebarItem {
		border: solid 1px rgba(10, 210, 10, 0.2);
		display: flex;
		flex-flow: column wrap;
		justify-content: space-between;
		min-width: 25vw;
		max-width: 25vw;
		padding: 1rem;
		width: 25vw;
	}
}

